import { Component } from '@angular/core';
import { NavController, ToastController, NavParams, Nav, IonicPage } from 'ionic-angular';
import { AppUpdate } from '@ionic-native/app-update';
import { BasePage } from '../base/base';
import { NetworkServiceProvider } from '../../providers/network-service/network-service';
import { AppGlobal, AppServiceProvider } from '../../providers/app-service/app-service';
@IonicPage()
@Component({
  selector: 'page-contact',
  templateUrl: 'contact.html'
})
export class ContactPage extends BasePage {

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl: ToastController,
    public appUpdate: AppUpdate,
    public nav:Nav,
    public net:NetworkServiceProvider,
    ) {
    super(navCtrl, navParams, toastCtrl);

  }

  update() {
    const updateUrl = AppGlobal.domain+"/mlAPP/version.xml";
    this.appUpdate.checkAppUpdate(updateUrl).then((res => {
      if (res.cole == 202 || res.code == '202') {
        this.toast("当前已是最新版本");
      }
    }), error => {
      console.log(error);
    })
  }
  clickLogout(){
    this.net.httpGet(
      AppGlobal.API.logout,
      AppServiceProvider.getInstance().userInfo,
      res => {
        AppServiceProvider.getInstance().userInfo = null;
        this.nav.setRoot("LoginPage");
      }, error => {}
    );
  }
}



